<template>
  <div>
    <h1>Create a New Category</h1>
    <form @submit.prevent="createCategory">
      <div>
        <label for="id">ID:</label>
        <input
          type="number"
          id="id"
          v-model="id"
          placeholder="Enter category ID"
          required
        />
      </div>
      <div>
        <label for="name">Name:</label>
        <input
          type="text"
          id="name"
          v-model="name"
          placeholder="Enter category name"
          required
        />
      </div>
      <div>
        <label for="description">Description:</label>
        <textarea
          id="description"
          v-model="description"
          placeholder="Enter category description"
          required
        ></textarea>
      </div>
      <button type="submit">Create</button>
    </form>
    <p v-if="message" :class="{ success: success, error: !success }">{{ message }}</p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      id: null, // Campo obligatorio
      name: '',
      description: '',
      message: '',
      success: false,
    };
  },
  methods: {
    async createCategory() {
      try {
        // Enviar el objeto Category al backend
        const response = await axios.post('http://localhost:18081/categories', {
          id: this.id,
          name: this.name,
          description: this.description,
          parentId: null, // Este campo no es necesario, pero lo dejamos explícito como null
        });
        this.message = `Category created successfully with ID: ${response.data}`;
        this.success = true;

        // Reiniciar el formulario después de crear la categoría
        this.id = null;
        this.name = '';
        this.description = '';
      } catch (error) {
        console.error(error);
        this.message = 'Failed to create category. Please try again.';
        this.success = false;
      }
    },
  },
};
</script>

<style scoped>
h1 {
  color: #333;
}

form {
  margin-top: 20px;
}

form div {
  margin-bottom: 1.5rem;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

input,
textarea {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

textarea {
  resize: vertical;
  height: 100px;
}

button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.message {
  margin-top: 1rem;
  font-size: 1.2rem;
  color: green;
}

.message.error {
  color: red;
}
</style>
